import React from 'react';
import '../css/CrossIcon.css'; // Make sure to create this CSS file

const CrossIcon = () => {
  return (
    <svg
      className="cross-icon"
      width="52"
      height="52"
      viewBox="0 0 52 52"
      xmlns="http://www.w3.org/2000/svg"
    >
      <circle cx="26" cy="26" r="25" fill="red" />
      <line
        x1="16"
        y1="16"
        x2="35"
        y2="35"
        stroke="white"
        strokeWidth="8"
        className="line line1"
      />
      <line
        x1="16"
        y1="35"
        x2="35"
        y2="16"
        stroke="white"
        strokeWidth="8"
        className="line line2"
      />
    </svg>
  );
};

export default CrossIcon;